import React, { Component } from "react";
import { Link } from "react-router-dom";
import CoursesData from '@data/courses';
import StarValue from "@components/StarValue";
import UdemySvg from '@assets/svg/udemy.svg';

import './index.scss';
import { connect } from "react-redux";
export const urlFromItem = (item: typeof CoursesData[0]) => {
    const { url, referralCode, couponCode } = item;
    const referralStr = referralCode
        ? ("referralCode=" + referralCode + "&")
        : "";
    const couponStr = couponCode
        ? ("couponCode=" + couponCode)
        : "";
    return `${url}?${referralStr}${couponStr}`;
}

const sWidth: number = window.innerWidth;

const mapStateToProps = (state: any) => ({
    cms: state.cms
})

const mapActionsToProps = {
}


const Courses: React.FC<any> = (props) => {
    const renderOneCourse = (item: any) => {
        const url = urlFromItem(item);
        return (
            <div className="col-lg-4 col-md-6 col-12 justify-content-center d-flex align-items-center"
                style={{
                    cursor: "pointer",
                    color: "black",
                }}>
                <a style={{ color: "black" }} href={url} target="_blank">
                    <div className="card courses-card">
                        <img src={item?.attributes?.thumbnail?.data?.attributes?.url.replace("https://s3.eu-central-1.amazonaws.com/", "https://") || "https://mapandan.gov.ph/wp-content/uploads/2018/03/no_image.jpg"}
                            className="card-img-top"
                            width="100%"
                            height="auto"
                            style={{
                                objectFit: "contain",
                                background: item?.attributes?.primaryColor
                            }} alt="..." />
                        <div className="card-body courses-body">
                            <h1>{item?.attributes?.title}</h1>
                            <p className="course-author"
                                onClick={() => {
                                    window.open(item?.attributes?.authorUrl, "__blank")
                                }}>
                                {item?.attributes?.author}
                            </p>
                            <p className="course-stars">
                                <span className="courseRating">{item?.attributes?.star}</span>
                                <span className="stars">{<StarValue value={item?.attributes?.starValue} />}</span>
                            </p>
                            <p className="course-information">Toplam {item?.attributes?.totalHours} - {item?.attributes?.totalCourses} Ders - {item?.attributes?.courseLevel}</p>
                            <p className="course-price">
                                {item?.attributes?.discountedPrice}
                                {item?.attributes?.discountedPrice
                                    ? (<span style={{
                                        textDecoration: "line-through",
                                        color: "#A7A7A7",
                                        marginLeft: "5px",
                                        marginTop: "-7px",
                                        fontSize: 17,
                                    }}>
                                        {item?.attributes?.price}
                                    </span>)
                                    : item?.attributes?.price}
                            </p>
                            <p className="course-registered">{item?.attributes?.registered} kayıt</p>

                        </div>
                    </div>
                </a>
            </div>
        )
    }
    return (
        <section className="courses" id="courses">
            {!props?.hideTitle && (
                <h2 className="courses-header">
                    <img src={UdemySvg}
                        width="128px" height="58px"
                        style={{ marginRight: "5px" }}
                    />
                </h2>)
            }
            <div className="container-fluid top-container" style={{
                ...props?.hideTitle && (
                    sWidth < 992 ? {
                        marginTop: "-60px"
                    } : {
                        marginTop: "-28px"
                    }
                )
            }}>
                <div className="courses-container">
                    <div className="container">
                        <div className="row justify-content-center d-flex align-items-center">
                            {props?.cms?.courses?.map((course, index) => {
                                return renderOneCourse(course)
                            })}
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default connect(mapStateToProps, mapActionsToProps)(Courses);