import { BsStarHalf, BsStar, BsStarFill } from "react-icons/bs";
function StarValue(props) {
    const value = props?.value;
    let calculatedValue = value;
    let isHalf = false;
    if (value % 1 < 0.25) {
        calculatedValue = Math.floor(value);
    } else if (value % 1 > 0.75) {
        calculatedValue = Math.ceil(value);
    } else {
        calculatedValue = Math.floor(value);
        isHalf = true;
    }
    const fullFilled: JSX.Element[] = new Array<JSX.Element>();
    for (let c = 0; c < calculatedValue; c++) {
        fullFilled.push(<BsStarFill className="course-star" />)
        //<i className="fas fa-star course-star"></i>
    }
    const unFilled: JSX.Element[] = new Array<JSX.Element>();
    for (let c = 0; c < ((5 - (isHalf === true ? 1 : 0)) - calculatedValue); c++) {
        unFilled.push(<BsStar className="course-star" />)
        //<i className="far fa-star course-star"></i>
    }
    const halfFilled: JSX.Element[] = new Array<JSX.Element>();
    for (let c = 0; c < (isHalf === true ? 1 : 0); c++) {
        halfFilled.push(<BsStarHalf className="course-star" />)
        //<i className="fas fa-star-half-alt"></i>
    }
    return (
        <span>
            {[...fullFilled, ...halfFilled, ...unFilled]}
        </span>
    )
}
export default StarValue;